@import "../../assets/styles/variables.scss";
.page-anypage {
	padding-bottom: 40px;
	@media (max-width: 30em) {
		padding-bottom: 20px;
	}
	// .page-anypage__inner
	&__inner {
		text-align: center;
	}
	// .page-anypage__image
	&__image {
		display: block;
		max-width: 25rem;
		margin: 0 auto 1em;
		img {
			max-width: 100%;
		}
	}
	// .page-anypage__content
	&__content {
		max-width: 54rem;
		margin: 0 auto;
	}
	// .page-anypage__title
	&__title {
		font-size: 4rem;
		line-height: 112%;
		color: $mainDarkColor;
		margin-bottom: 0.125em;
		span {
			color: $accentColor;
			text-transform: uppercase;
		}
		@media (max-width: 62em) {
			font-size: 3rem;
		}
		@media (max-width: 30em) {
			font-size: 2.5rem;
		}
	}
	// .page-anypage__subtitle
	&__subtitle {
		margin-bottom: 2em;
		@media (max-width: 30em) {
			margin-bottom: 1em;
		}
	}
	// .page-anypage__button
	&__button {
	}
}
